import { computed, defineComponent } from 'vue';
import { formconfig } from './config/form.config';
import { tableconfig } from './config/table.config';
import { modalconfig } from './config/modal.config';
import PageSearch from '@/components/page-search';
import PageContnet from '@/components/page-content';
import PageModal from '@/components/page-modal';
import { usePageSearch } from '@/hooks/use-page-search';
import { usePageModal } from '@/hooks/use-page-modal';
import { useStore } from 'vuex';
export default defineComponent({
    name: 'user',
    components: {
        PageSearch,
        PageContnet,
        PageModal
    },
    setup() {
        let [pageContentRef, handleResetEvent, handlesearchEvent] = usePageSearch();
        // 更新时隐藏password 输入项
        const updateCb = () => {
            const passwordItem = modalconfig.formItems.find((item) => {
                return item.field === 'password';
            });
            if (passwordItem) {
                passwordItem.isHidden = true;
            }
        };
        const createCb = () => {
            const passwordItem = modalconfig.formItems.find((item) => {
                return item.field === 'password';
            });
            if (passwordItem) {
                passwordItem.isHidden = false;
            }
        };
        let [pageModalRef, defaultInfo, handleCreate, handleUpdate] = usePageModal(updateCb, createCb);
        const modalconfigRef = computed(() => {
            const store = useStore();
            const departmentItem = modalconfig.formItems.find((item) => {
                return item.field === 'departmentId';
            });
            const roleItem = modalconfig.formItems.find((item) => {
                return item.field === 'roleId';
            });
            departmentItem.options = store.state.entireDepartment.map((item) => {
                return { label: item.name, value: item.id };
            });
            roleItem.options = store.state.entireRole.map((item) => {
                return { label: item.name, value: item.id };
            });
            return modalconfig;
        });
        return {
            formconfig,
            tableconfig,
            modalconfigRef,
            pageContentRef,
            handlesearchEvent,
            handleResetEvent,
            pageModalRef,
            handleUpdate,
            handleCreate,
            defaultInfo
        };
    }
});
